import React from "react";
import {
  View,
  Group,
  Epic,
  Tabbar,
  TabbarItem,
  ConfigProvider,
} from "@vkontakte/vkui";
import "@vkontakte/vkui/dist/vkui.css";
import bridge from "@vkontakte/vk-bridge";

import Main from "./panels/main/main.js";
import Top from "./panels/top/top.js";

import { Icon56DiamondOutline } from "@vkontakte/icons";
import { Icon56UserCircleOutline } from "@vkontakte/icons";
bridge.send("VKWebAppInit", {});

let active = "main";
let colormain = "#FFFFFF";
let colortop = "#808080";
function move_main() {
  active = "main";
  colortop = "#808080";
  colormain = "#FFFFFF";
}
function move() {
  active = "top";
  colormain = "#808080";
  colortop = "#FFFFFF";
}
class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      activePanel: "main",
    };
  }

  render() {
    return (
      <Group>
        <ConfigProvider scheme="space_gray">
          <View activePanel={active}>
            <Top id="top" />
            <Main id="main" />
          </View>

          <Epic>
            <Tabbar>
              <TabbarItem
                style={{ color: colortop }}
                onClick={() => move()}
                text="–¢–æ–ø"
              >
                <Icon56DiamondOutline width={28} height={28} />
              </TabbarItem>
              <TabbarItem
                style={{ color: colormain }}
                onClick={() => move_main()}
                text="–ü—Ä–æ—Ñ–∏–ª—å"
              >
                <Icon56UserCircleOutline width={28} height={28} />
              </TabbarItem>
            </Tabbar>
          </Epic>
        </ConfigProvider>
      </Group>
    );
  }
}
export default App;
